.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

h1{
  font-family: "Bigelow Rules";
  font-size: 72px;
  color: #0ccac4;
}

button {
  background: linear-gradient(
    to left,
    rgba(7, 27, 82, 0.8) 0%,
    rgba(0, 128, 128, 0.8) 100%
  );
  padding: 10px 20px;
  margin: 10px;
  border-radius: 10px;
  font-size: 14px;
  border: none;
  display: inline-block;
}

button:hover {
  cursor: pointer;
  color: white;
  border: none;
}

border:active, border:visited{
  border: none;
}